import React from 'react';
import { default as MuiLinearProgress, LinearProgressProps } from '@mui/material/LinearProgress';

const LinearProgress = React.forwardRef<HTMLElement, LinearProgressProps>( ( props, ref ) => {
	return (
		<MuiLinearProgress
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<LinearProgressProps>;

export default LinearProgress;
